
import Vue, { PropType } from "vue"

export default Vue.extend({
  props: {
    // String
    href: {
      type: [] as PropType<any>,
      required: false,
      default: null,
    },
    icon: {
      type: String,
      required: false,
      default: null,
    },

    // Boolean
    block: {
      type: Boolean,
      required: false,
      default: false,
    },
    rounded: {
      type: Boolean,
      required: false,
      default: false,
    },
    blank: {
      type: Boolean,
      required: false,
      default: false,
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  computed: {
    getIconName(): string {
      return this.icon?.startsWith("Icon") ? this.icon : `Icon${this.icon}`
    },
  },
})
